.comic {
    display: grid;
    grid-template-columns: repeat(auto-fit, 500px);
    -ms-flex-pack: center;
        justify-content: center;
}
@media (max-width: 500px) {
    .comic {
      grid-template-columns: 300px;
    }
    .comic .vignette .vignette__illustration {
      margin-top: 5rem;
    }
}


